.waivy {
  -webkit-box-reflect: below -65px linear-gradient(to bottom, rgba(0, 0, 0, 0)
        25%, rgba(0, 0, 0, 0.9) 95%);
  letter-spacing: 1px;
}
.waivy span {
  font-size: 100px;
  position: relative;
  display: inline-block;
  background: -webkit-linear-gradient(115deg, #20bf55 0%, #01baef 74%);
  filter: drop-shadow(8px 3px 3px rgb(0, 242, 255));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.one {
  animation: waviy 1s 2;
  animation-delay: calc(0.1s * 1);
}
.two {
  animation: waviy 1s 2;
  animation-delay: calc(0.1s * 2);
}
.three {
  animation: waviy 1s 2;
  animation-delay: calc(0.1s * 3);
}
.four {
  animation: waviy 1s 2;
  animation-delay: calc(0.1s * 4);
}
.five {
  animation: waviy 1s 2;
  animation-delay: calc(0.1s * 5);
}
.six {
  animation: waviy 1s 2;
  animation-delay: calc(0.1s * 6);
}
.seven {
  animation: waviy 1s 2;
  animation-delay: calc(0.1s * 7);
}
.eight {
  animation: waviy 1s 2;
  animation-delay: calc(0.1s * 8);
}
.nine {
  animation: waviy 1s 2;
  animation-delay: calc(0.1s * 9);
}
.ten {
  animation: waviy 1s 2;
  animation-delay: calc(0.1s * 10);
}
.eleven {
  animation: waviy 1s 2;
  animation-delay: calc(0.1s * 11);
}
.twelve {
  animation: waviy 1s 2;
  animation-delay: calc(0.1s * 12);
}
@keyframes waviy {
  0%,
  40%,
  100% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(-20px);
  }
}

